body {
  margin: 0;
  padding: 0;
  background: black;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background: url(/iry/static/media/bg.4f917206.jpg) no-repeat center center fixed;
  background-size: cover;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  vertical-align: middle;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-animation: Background-fade 5s ease-in;
          animation: Background-fade 5s ease-in;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 100px;
  pointer-events: none;
}

.App-header {
  min-height: 120px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link,
.App-link:active,
.App-link:visited {

  color: white;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes Background-fade {
  from { 
    opacity: 0;
    scale: 1;
   }
  to { 
    opacity: 1;
    scale: 1.2;   
  }
}

@keyframes Background-fade {
  from { 
    opacity: 0;
    scale: 1;
   }
  to { 
    opacity: 1;
    scale: 1.2;   
  }
}

@-webkit-keyframes Content-fade {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes Content-fade {
  from { opacity: 0; }
  to { opacity: 1; }
}

